import { redirect } from "next/navigation";
import { getCurrentUser } from "@/lib/session";
import { prisma } from "@/lib/prisma";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { IncomeTable } from "@/components/finance/income-table";
import { AddIncomeDialog } from "@/components/finance/add-income-dialog";
import { ExpenseTable } from "@/components/finance/expense-table";
import { AddExpenseDialog } from "@/components/finance/add-expense-dialog";
import { InvestmentTable } from "@/components/finance/investment-table";
import { AddInvestmentDialog } from "@/components/finance/add-investment-dialog";
import { MiscExpenseTable } from "@/components/finance/misc-expense-table";
import { AddMiscExpenseDialog } from "@/components/finance/add-misc-expense-dialog";
import { PlanNextMonthAlert } from "@/components/finance/plan-next-month-alert";

interface MonthPageProps {
    params: Promise<{
        year: string;
        month: string;
    }>;
}

export default async function MonthPage({ params }: MonthPageProps) {
    const user = await getCurrentUser();

    if (!user) {
        redirect("/login");
    }

    const { year: yearStr, month: monthStr } = await params;
    const year = parseInt(yearStr);
    const month = parseInt(monthStr);

    const monthData = await prisma.month.findFirst({
        where: {
            userId: user.id,
            year: year,
            month: month,
        },
        include: {
            incomes: { orderBy: { order: "asc" } },
            expenses: { orderBy: { order: "asc" } },
            investments: { orderBy: { order: "asc" } },
            miscExpenses: { orderBy: { order: "asc" } },
        },
    });

    if (!monthData) {
        return (
            <div className="container mx-auto p-4 text-white">
                <p>Mês não encontrado.</p>
                <Link href="/">
                    <Button variant="link" className="text-primary-light">Voltar</Button>
                </Link>
            </div>
        );
    }

    const totalIncome = monthData.incomes.reduce((acc, item) => acc + Number(item.amount), 0);
    const totalExpense = monthData.expenses.reduce((acc, item) => acc + Number(item.totalAmount), 0);
    const balance = totalIncome - totalExpense;

    // Navigation logic
    let prevMonth = month - 1;
    let prevYear = year;
    if (prevMonth < 1) {
        prevMonth = 12;
        prevYear -= 1;
    }

    let nextMonth = month + 1;
    let nextYear = year;
    if (nextMonth > 12) {
        nextMonth = 1;
        nextYear += 1;
    }

    return (
        <div className="min-h-screen bg-background-dark font-display pb-24">
            {/* Header */}
            <div className="flex items-center justify-between p-4 pb-2">
                <Link href={`/${prevYear}/${prevMonth}`}>
                    <button className="flex items-center justify-center size-12 text-white/80 hover:bg-white/10 rounded-full transition-colors">
                        <span className="material-symbols-outlined text-2xl">arrow_back_ios</span>
                    </button>
                </Link>
                <h2 className="text-white text-lg font-bold leading-tight tracking-[-0.015em] flex-1 text-center capitalize">
                    {new Date(year, month - 1).toLocaleString("pt-BR", { month: "long", year: "numeric" })}
                </h2>
                <Link href={`/${nextYear}/${nextMonth}`}>
                    <button className="flex items-center justify-center size-12 text-white/80 hover:bg-white/10 rounded-full transition-colors">
                        <span className="material-symbols-outlined text-2xl">arrow_forward_ios</span>
                    </button>
                </Link>
            </div>

            {/* Balance */}
            <h1 className="text-white tracking-tight text-[40px] font-bold leading-tight px-4 text-center pt-6">
                {new Intl.NumberFormat("pt-BR", { style: "currency", currency: "BRL" }).format(balance)}
            </h1>
            <p className="text-primary-light text-sm font-normal leading-normal pb-3 pt-1 px-4 text-center">
                Saldo do Mês
            </p>

            {/* Summary Cards */}
            <div className="grid grid-cols-2 gap-4 px-4 pt-6">
                {/* Income Card */}
                <div className="flex flex-col gap-2 rounded-xl bg-accent-green p-4 shadow-lg shadow-accent-green/20">
                    <div className="flex items-center gap-2">
                        <div className="flex size-8 shrink-0 items-center justify-center rounded-full bg-white/20 text-white">
                            <span className="material-symbols-outlined text-base">arrow_upward</span>
                        </div>
                        <p className="text-white/70 text-sm font-medium">Entradas</p>
                    </div>
                    <p className="text-white text-xl font-bold leading-tight">
                        {new Intl.NumberFormat("pt-BR", { style: "currency", currency: "BRL" }).format(totalIncome)}
                    </p>
                </div>

                {/* Expense Card */}
                <div className="flex flex-col gap-2 rounded-xl bg-accent-red p-4 shadow-lg shadow-accent-red/20">
                    <div className="flex items-center gap-2">
                        <div className="flex size-8 shrink-0 items-center justify-center rounded-full bg-white/20 text-white">
                            <span className="material-symbols-outlined text-base">arrow_downward</span>
                        </div>
                        <p className="text-white/70 text-sm font-medium">Saídas</p>
                    </div>
                    <p className="text-white text-xl font-bold leading-tight">
                        {new Intl.NumberFormat("pt-BR", { style: "currency", currency: "BRL" }).format(totalExpense)}
                    </p>
                </div>
            </div>

            {/* Plan Next Month Alert */}
            <div className="px-4 mt-4">
                <PlanNextMonthAlert latestMonth={monthData} />
            </div>

            {/* Transactions Sections */}
            <div className="flex flex-col px-4 pt-6 gap-8">

                {/* Incomes */}
                <div>
                    <div className="flex items-center justify-between mb-4">
                        <h3 className="text-lg font-bold text-white">Entradas</h3>
                        <AddIncomeDialog monthId={monthData.id} />
                    </div>
                    <div className="bg-surface-dark rounded-xl p-2">
                        <IncomeTable incomes={monthData.incomes} />
                    </div>
                </div>

                {/* Expenses */}
                <div>
                    <div className="flex items-center justify-between mb-4">
                        <h3 className="text-lg font-bold text-white">Saídas</h3>
                        <AddExpenseDialog monthId={monthData.id} />
                    </div>
                    <div className="bg-surface-dark rounded-xl p-2">
                        <ExpenseTable expenses={monthData.expenses} />
                    </div>
                </div>

                {/* Investments */}
                <div>
                    <div className="flex items-center justify-between mb-4">
                        <h3 className="text-lg font-bold text-white">Investimentos</h3>
                        <AddInvestmentDialog monthId={monthData.id} />
                    </div>
                    <div className="bg-surface-dark rounded-xl p-2">
                        <InvestmentTable investments={monthData.investments} />
                    </div>
                </div>

                {/* Misc Expenses */}
                <div>
                    <div className="flex items-center justify-between mb-4">
                        <h3 className="text-lg font-bold text-white">Gastos Avulsos</h3>
                        <AddMiscExpenseDialog monthId={monthData.id} />
                    </div>
                    <div className="bg-surface-dark rounded-xl p-2">
                        <MiscExpenseTable miscExpenses={monthData.miscExpenses} />
                    </div>
                </div>

            </div>

            {/* Floating Action Button (Optional, if we want a global add) */}
            {/* <div className="fixed bottom-6 right-6">
        <button className="flex size-14 cursor-pointer items-center justify-center overflow-hidden rounded-full bg-accent-green text-black shadow-lg shadow-accent-green/30 transition-transform hover:scale-105 active:scale-95">
          <span className="material-symbols-outlined text-3xl">add</span>
        </button>
      </div> */}
        </div>
    );
}
